<template>
  <div class="divh">
<span class="headerSpan">明细-统计页</span>
</div>

<div class="editContent">
  <!-- 时间选择 -->
<div class="editchoosetime">
 <span>日期：</span>
  <time-selection @getTime="getStaticTime"></time-selection>
</div>

</div>

<div class="stadivbtn">
<van-button plain type="primary" @click="showSpend" class="showCostBtn" color="#39be77"
:class="{activespend:isSpend}"
    >支出</van-button
  >
  <van-button plain type="primary" @click="showEarn" class="showCostBtn" color="#39be77"
  :class="{activeEarn:isEarn}"
    >收入</van-button
  >
 <van-button plain type="primary" @click="showYear" class="showItemBtn" color="#39be77"
 :class="[{activespend:isSpend},{activeEarn:isEarn},{activeYearSelf:isYearSelf}]"
      >{{currentYear}}年</van-button
    >
    <van-button plain type="primary" @click="showMonth" class="showItemBtn" color="#39be77"
    :class="[{activespend:isSpend},{activeEarn:isEarn},{activeMonthself:isMonthSelf}]"
      >{{currentMonth}}月</van-button
    >
    <van-button plain type="primary" @click="showDay" class="showItemBtn" color="#39be77"
    :class="[{activespend:isSpend},{activeEarn:isEarn},{activeDaySelf:isDaySelf}]"
      >{{currentday}}日</van-button
    >
  <!-- 需要先点击 支付/收入按钮，再点击 年/月/日 按钮 -->
<van-dialog
      v-model:show="staticBtnShow"
      :title="dialogBtnMsg"
      showConfirmButton
      messageAlign="center"
    >
    </van-dialog>


  <!-- 列表：本年-->
  <!-- 第一次循环：天数，第二次循环：每天的：衣食住行 -->
  <div v-show="showYearCell">
    <van-cell-group  :title="yearSpendAll">
      <van-cell
        v-for="yearItem in yearItems"
        :key="yearItem.time.getDate()"
        :title="yearItem.item"
        :value="'￥'+yearItem.money"
        :label="yearItem.msg"
      />
    </van-cell-group>
  </div>


  <!-- 列表：本月 -->
<div v-show="showMonthCell">
    <van-cell-group   :title="monthSpendAll">
       <van-cell
        v-for="monthItem in monthItems"
        :key="monthItem.time.getDate()"
        :title="monthItem.item"
        :value="'￥'+monthItem.money"
        :label="monthItem.msg"
      />
    </van-cell-group>
  </div>
  <!-- 列表：本日 -->
<div v-show="showDayCell">
    <van-cell-group :title="daySpendAll">
      <van-cell
      v-for="dayItem in dayItems"
      :key="dayItem.time.getDate()"
        :title="dayItem.item"
        :value="'￥'+dayItem.money"
        :label="dayItem.msg"
      />
    </van-cell-group>
    </div>
  </div>
  <nav-bar></nav-bar>
</template>
<script>
import navBar from "../components/navBar.vue";
import timeSelection from "../components/timeSelection.vue";

export default {
  name: "CostStatics",

  data() {
    return {
      show: false,
      spendEarn: false,
      // yearBtn: false,
      monthBtn: false,
      costItems: [],
      dayItems: [],
      yearItems: [],
      monthItems: [],
      showYearCell:true,
      showMonthCell:false,
      showDayCell:false,

      currentYear:"",
      currentMonth:"",
      currentday:"",

      currentDateshow:true,
      currentDate:"",

      isSpend:false,
      isEarn:false,

      isYearSelf:false,
      isMonthSelf:false,
      isDaySelf:false,

      staticBtnShow:false,
      dialogBtnMsg:""
    };
  },

  components: { navBar,timeSelection },
  methods: {
    setAdd() {
      this.show = true;
    },

    // 过滤： 生成dayItem,yearItem,monthItem
    fliterItem(items){
      // 生成yearItem列表
        // const currentYear =  (new Date()).getFullYear();
        for (let i = items.length - 1; i > -1; i--) {
          if (items[i].time.getFullYear() == this.currentYear) {
            this.yearItems.push(items[i]);
          }
        }
      //生成monthItem列表
        // const currentMonth = (new Date()).getMonth() + 1;
      // console.log("currentmonth" + currentMonth);
      for (let i = 0; i < this.yearItems.length; i++) {
        if (this.yearItems[i].time.getMonth() + 1 == this.currentMonth) {
          this.monthItems.push(this.yearItems[i]);
        }
      }

      //生成dayItem列表
        // const currentday =  (new Date()).getDate();
        // console.log("currentday" + currentday);  
        for (let i = 0; i < this.monthItems.length; i++) {
          if (this.monthItems[i].time.getDate() == this.currentday) {
            this.dayItems.push(this.monthItems[i]);
            // console.log("day"+this.dayItem);
          }
        
      } 

    },
    // 获取时间
    getStaticTime(time){
      console.log(time);
      this.currentYear = time.getFullYear();
      this.currentMonth = time.getMonth() + 1;
      this.currentday = time.getDate();

      console.log(this.currentYear);
      console.log(this.currentMonth);
      console.log(this.currentday);

    },

    showSpend() {
      // 切换样式：
      this.isSpend =true;
      this.isEarn = false;
      // 切换store的EditSpendItems列表
      this.spendEarn = true;
      this.costItems = this.$store.getters.getEditSpendItems;
      //this.costItems此时是一个数组。store的EditSpendItems
      // 每次点击都重新过滤一次
      this.dayItems=[];
      this.yearItems=[];
      this.monthItems=[];
      this.fliterItem(this.costItems);
      // console.log(this.costItems.length);
    },
    showEarn() {
      this.isSpend =false;
      this.isEarn =true;

      this.spendEarn = true;
      this.costItems = this.$store.getters.getEditEarnItems;
      // 每次点击都重新过滤一次
      this.dayItems=[];
      this.yearItems=[];
      this.monthItems=[];

      this.fliterItem(this.costItems);
      // console.log(this.costItems.length);
    },

    showYear() {
      // this.spendEarn 为true时代表选择了 支付/收入
       if (!this.spendEarn) {
          this.dialogBtnMsg="请选择：支付/收入";
        return this.staticBtnShow=true;
      };
          
          this.showYearCell=true;
           this.showMonthCell=false;
           this.showDayCell=false;
      
      this.isYearSelf=true;
       this.isMonthSelf=false;
        this.isDaySelf=false;
        console.log( this.isYearSelf);
      
    },
    showMonth() {
      if (!this.spendEarn) {
          this.dialogBtnMsg="请选择：支付/收入";
        return this.staticBtnShow=true;
      };
     
           this.showYearCell=false;
           this.showMonthCell=true;
           this.showDayCell=false;

          this.isMonthSelf=true;
           this.isYearSelf=false;
           this.isDaySelf=false;

    },

    showDay() {
        if (!this.spendEarn) {
          this.dialogBtnMsg="请选择：支付/收入";
        return this.staticBtnShow=true;
      };

        this.showYearCell=false;
           this.showMonthCell=false;
           this.showDayCell=true;

           this.isDaySelf=true;
           this.isMonthSelf=false;
            this.isYearSelf=false;
      
    },
  },
  computed:{
    yearSpendAll() {
          let count=0;   
          // let time;    
          for(let i=0; i<this.yearItems.length;i++){
          count += parseFloat(this.yearItems[i].money) ; 
          // time=this.yearItems[i].time.getFullYear();
          };
          return this.currentYear+'年'+'：总支出：￥'+count;
    },
    monthSpendAll(){
      let count=0;       
       let time;  
          for(let i=0; i<this.monthItems.length;i++){
          count += parseFloat(this.monthItems[i].money) ; 
          time=this.yearItems[i].time.getMonth() + 1; 
          };
          return this.currentMonth+'月'+'：总支出：￥'+count;
    },
    daySpendAll(){
       let count=0;       
      //  let time;  
          for(let i=0; i<this.dayItems.length;i++){
          count += parseFloat(this.dayItems[i].money) ;
          // time=this.yearItems[i].time.getDate(); 
          };
          return this.currentday +'日'+'：总支出：￥'+count;
    }


  }

};
</script>
<style>
.showCostBtn {
  width: 50%;
}
.showItemBtn {
  width: 33.3%;
}
.stadivbtn{
  font-size: 18px;
}
.stadivbtn .activespend{
  background-color:rgba(106, 160, 231, 0.3);
}


.stadivbtn .activeEarn{
  background-color:rgba(57, 190, 119, 0.3);
  
}

.stadivbtn .activeYearSelf{
 font-weight: bold;
 font-size: 20px;
}
.stadivbtn .activeMonthself{
   font-weight: bold;
 font-size: 20px;
}
.stadivbtn .activeDaySelf{
   font-weight: bold;
 font-size: 20px;
}
</style>



