<template>
<span  @click="showPopup">{{selectionTime}}</span><div class="jian"></div>

  <!-- 设置时间选择器的弹出位置：从下面弹出 -->
  <van-popup v-model:show="show" position="bottom" :style="{ height: '50%' }">
    <!-- 时间选择器组件 ：vant时间选择 -->
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title="选择年月日"
      :min-date="minDate"
      :max-date="maxDate"
      :formatter="formatter"
      @confirm="chooseTime"
      @cancel="back"   
    />
    
  </van-popup>

</template>

<script>
import { onMounted, ref } from "vue";
import dayjs from "dayjs";

export default {
  name: "timeSelection",
  emits:['getTime'],
  
  setup(props, ctx) {
    //明细中的日期选择。逻辑部分：
    const currentDate = ref(new Date());
    //设置日期选择器的日期的格式化
    const formatter = (type, val) => {
      if (type === "year") {
        return `${val}年`;
      }
      if (type === "month") {
        return `${val}月`;
      }
      if (type === "day") {
        return `${val}日`;
      }
      return val;
    };


    //设置日期选择器的弹出
    const show = ref(false);
    const showPopup = () => {
      show.value = true;

    };

    //设置日期选择的“取消”-cancel，“确认”-confirm，“改变”-change
    // dafautime默认值，即显示的日期：为当天  
    // costTime  选择的时间。用于传递Costs组件，未格式化处理。   
    
      const costTime = ref(new Date());
    
      // console.log("默认"+costTime.value);
      const defautime =dayjs(costTime.value).format('YYYY-MM-DD')
    //selectionTime为本组件内使用的时间，不进行传递，格式化处理，响应式
    const selectionTime = ref(defautime);
    const chooseTime = (item) => {
      //  console.log(item.getMonth()+1); //month为0-11
      show.value = false;  
      costTime.value = item;
     
      //此时通过vant组件的confirm事件传递的item（时间），是标准格式，用dayjs进行转换
      selectionTime.value = dayjs(item).format('YYYY-MM-DD');
      // console.log("更改"+costTime.value);
      // console.log(costTime.value.getMonth()+1);
      ctx.emit('getTime',costTime.value);
      
    };

    const back = () => {
      show.value = false;
    };
    onMounted(()=>{
      chooseTime(costTime.value);
      //  console.log('Component is mounted!')
      });

    return {
      minDate: new Date(2021, 1, 1),
      maxDate: new Date(2025, 12, 31),
      formatter,
      currentDate,
      chooseTime,
      show,
      showPopup,
      back,    
      selectionTime,  
      costTime,
    };
  },
};
</script>


<style>
.jian{
  /* 自定义三角图标，向下 */
  width: 0;
  height:0;
  border-top:10px solid black;
  border-right: 10px solid transparent;
  border-left:10px solid transparent; ;
  border-bottom: 0px ;
  display: inline-block;
  margin-right:20px;
}


</style>
