<template>
    <!-- 登录界面——暂时没开发此页面 -->
    <header :title="type == 'login' ? '登录' : '注册'"/>

    <div> 
      <!-- 待定：无法加载图片 -->
      <!-- <image width="10rem" height="10rem" src="../assets/jizhang.jpeg"/> -->
<van-form @submit="onSubmit" v-if="type == 'login'">
     <h3>登录</h3>
  <van-cell-group inset>
    <van-field
      v-model="username"
      name="用户名"
      label="用户名"
      placeholder="请填写用户名"
      :rules="[{ required: true, message: '请填写用户名' }]"
    />
    <van-field
      v-model="password"
      type="password"
      name="密码"
      label="密码"
      placeholder="请填写密码"
      :rules="[{ required: true, message: '请填写密码' }]"
    />
  </van-cell-group>
  <div style="margin: 16px;">
    <van-button round block type="primary" native-type="submit">
      登录
    </van-button>
   <p @click="changeType('register')" class="change-btn">没有账户，点击前往注册</p>
  </div>
</van-form>
</div>

<!-- 注册页面 -->

    <div>
       <van-form @submit="onSubmit" v-if="type == 'register'">
           <h3>注册</h3>
  <van-cell-group inset>
    <van-field
      v-model="username"
      name="用户名"
      label="用户名"
      placeholder="请填写用户名"
      :rules="[{ required: true, message: '请填写用户名' }]"
    />
    <van-field
      v-model="password"
      type="password"
      name="密码"
      label="密码"
      placeholder="请填写密码"
      :rules="[{ required: true, message: '请填写密码' }]"
    />
   <!-- 待定功能：验证码 -->
  </van-cell-group>
  <div style="margin: 16px;">
    <van-button round block type="primary" native-type="submit">
      注册
    </van-button>
    <p @click="changeType('login')" class="change-btn">已有账户，点击前往登录</p>
  </div>
</van-form>
    </div>

</template>

<script>
export default {
    name:"login",
    data(){
        return{
            username:'',
            password:'',
            type:'login',
            users:[],
            // type选择：'login'和'register'  v-if切换
        }
    },
    methods:{
onSubmit:function () {
//条件判断：type为'register'向users数组添加注册的用户：username和password
//type为'login'时，根据表单username和password，循环遍历users数组进行匹配，成功就路由导向home页面
 
if(this.type == 'register'){
  let user = {};
  user.username = this.username;
  user.password = this.password;
  this.users.push(user);
  return this.type = 'login';
  }

if(this.type == 'login'){
for (let i=0;i<this.users.length;i++){
  if(this.username == this.users[i].username){
    if(this.password == this.users[i].password){
      return this.$router.push('/');
    }
  }}
}},
changeType:function(type) {
  this.type = type
}
    }
};
</script>

<style>
h3{
    color:blue
}
.change-btn {
      text-align: center;
      margin: 10px 0;
      /* color:; */
      font-size: 14px;
    }
</style>
