<template>
  <!-- 主界面：注册登录后的标签界面 -->
    <!-- <h2>这是Costs---标签页面{{$store.state.details}}</h2> -->
<div class="divh">
<span class="headerSpan">标签页</span>
</div>
    <div class="costContent">
    <van-cell-group inset >
      <van-cell
        v-for="detail in $store.state.details"
        :key="detail"
        :title="detail"
        is-link
        class="detailCell"
        @click="cellClick"
      />
    </van-cell-group>

<!-- 标签---修改/删除按钮 -->

    <van-popup v-model:show="popupShow" position="right" :style="{ width: '30%' }" class="popupBtn" >
      <van-button  type="primary" @click="changeCellBtn"  color="#39be77" class="CellBtn">修改标签</van-button>
      <van-button  type="primary" @click="delateCellBtn"  color="linear-gradient(to right, #ff6034, #ee0a24)" class="CellBtn">删除标签</van-button>
      </van-popup>
<!-- 修改按钮dialog -->
<van-dialog v-model:show="CellBtnshow" title="修改标签"  show-cancel-button @confirm="detailChange">
  <van-cell  :title="'旧标签：'+cellValue"></van-cell>
 <van-field v-model="changeValue" label="新标签" placeholder="请输入新标签名" /> 
</van-dialog>

    
<!-- 添加标签按钮 -->
<van-button  type="primary" @click="showdialog"  color="#39be77" class="dialogBtn">添加标签</van-button>

    <!-- 在vue3中，van-dialog和van-popup组件的v-model="show"均需要做修改vue3语法 -->
<van-dialog v-model:show="show" title="添加标签" show-cancel-button @confirm="detailAdd">
 <van-field v-model="value" label="" placeholder="请输入标签名" /> 
</van-dialog>
</div>
    <nav-bar></nav-bar>

</template>

<script>
import navBar from "../components/navBar.vue";


export default {
  name: "Costs",
  components: { navBar },
  emits:['showdialog'],
  data() {
    return {
      
      show:false,
      popupShow:false,
      CellBtnshow:false,
      cellValue:"",
      changeValue:"",
      value: "",
    }
  },
  methods: {

    showdialog() {
      this.show = true;
    },
    detailAdd(){
     
      this.$store.commit('addDetails',this.value)
    },
    changeCellBtn(){
        this.CellBtnshow = true;       
    },
    detailChange(){

  for (let i=0;i<this.$store.state.details.length;i++){
        if(this.$store.state.details[i] == this.cellValue ){
          // 替换一个元素。vue扩展了数组的splice方法，会响应式的更新数组元素。---更新视图
         this.$store.commit('changeDetails',{
           num:i,
           string:this.changeValue
         });
          return this.popupShow=false; 
        }
      }
    },
    delateCellBtn(){
        for (let i=0;i<this.$store.state.details.length;i++){
        if(this.$store.state.details[i] == this.cellValue ){
          // 删除该项（一项）
          this.$store.commit('delateDetails',i);
          return this.popupShow=false; 
        }
      }
    },

    cellClick(e){
      console.log(e.target.innerText);
      this.cellValue=e.target.innerText;
      this.popupShow=true;  
    }

  },
};
</script>

<style>
.costContent{
  font-size: 18px;
}

.dialogBtn{
  position: absolute;
  bottom: 30%;
  left: 35%;
}
.detailCell{
  font-size: 18px;
}
.popupBtn{
  background-color: transparent;
  margin-top:-30%;
}
.CellBtn{
  margin-top: 20px;
}

</style>



