<template>
  <div class="divh">
<span class="headerSpan">记账页</span>
</div>

<div class="editContent">
<div class="editchoosetime">
  <span>日期：</span>
  <time-selection @getTime="getCostTime"></time-selection>
</div>
  <div>
    <ol @click="editChoose" class="editdetails">
      <!-- 标签列表项 -->
      <li
        v-for="item in $store.state.details"
        :key="item"
        :title="item"
        class="editdetail"
        :class="{active:editChooseDetail==item}"
      >
        {{ item }}
      </li>
    </ol>
    <div class="editdivmsg">
      <van-field
        v-model="editMsg"
        label="备注"
        v-show="editshow"
        placeholder="可以在这里添加备注"
        label-width="4em"
      />
    </div>
    <div>
      <!-- 输入金额 -->
      <van-field
        v-model="editMoney"
        type="number"
        label="￥"
        placeholder="请输入金额"
        label-width="2em"
        label-align="center"
        size="large"
        @click="show = true"
      />
    </div>

    <van-button plain type="primary" @click="editSpend" class="editBtn" color="#39be77"
      >支出</van-button
    >
    <van-button plain type="primary" @click="editEarn" class="editBtn" color="#39be77"
      >收入</van-button
    >
    </div>
    <van-dialog
      v-model:show="editDiaShow"
      :title="'提示 : ' + dialogMsg"
      showConfirmButton
      messageAlign="center"
    >
    </van-dialog>

<!-- 点击支付/收入按钮弹出的dialog -->
    <van-dialog
      v-model:show="editBtnItem"
      title="记一笔！"
      show-cancel-button
      messageAlign="center"
      @confirm="addItem"
      
    >
    <div class="SEdialog">
 <label class="SEdialogItem">时间：</label><label>{{timeFormt}}</label>
    <label class="SEdialogItem">标签：</label><label>{{editChooseDetail}}</label>
    <label class="SEdialogItem">金额：</label><label>￥{{editMoney }}</label>
    <label class="SEdialogItem">备注：</label><label>{{editMsg }}</label>    
    </div>
     
    </van-dialog>

    <!-- 数字键盘 -->
    <van-number-keyboard
      v-model="editMoney"
      :show="show"
      theme="custom"
      extra-key="."
      close-button-text="完成"
      @blur="show = false"

    />
  </div>

  <nav-bar></nav-bar>
</template>

<script>
import navBar from "../components/navBar.vue";
import timeSelection from "../components/timeSelection.vue";
import dayjs from "dayjs";

export default {
  name: "detailsAdd",
  components: { navBar, timeSelection },

  data() {
    return {
      //value为键盘/金额项输入金额
      //show为键盘的显示/隐藏
      editMsg: "",
      editshow: true,
      editMoney: "",
      editDiaShow: false,
      editChooseDetail: "",
      show: false,
      dialogMsg: "",
    
      editBtnType: "",
      addEditSpend: {},
      
      editBtnItem: false,
      editBtnItemMsg: "",
      editTime: new Date(),
      
      timeFormt:"",
    };
  },

  methods: {
    // 消费类型选择：
    editChoose(e) {
      console.log(e.target.title);
      this.editChooseDetail = e.target.title;
    
    },
    //获取时间(未格式化处理的时间),this.editTime已设置默认值new Date()
    getCostTime(time) {
      this.editTime = time;
      // console.log(this.editTime);
    },

    // editSEformat：格式化支付/收入按钮所呈现的dialog内容
    editSEformat(){
      this.timeFormt = dayjs(this.editTime).format('YYYY-MM-DD')
       
    },

    //editSpend 选择记录支付
    editSpend: function () {
      if (!this.editChooseDetail) {
        this.editDiaShow = true;
        this.dialogMsg = "请选择消费类型";
        return;
      }
      if (!this.editMoney) {
        this.editDiaShow = true;
        this.dialogMsg = "请输入消费金额";
        return;
      }

      // console.log(this.addEditSpend.time);
      this.editBtnItem = true;
      // this.addchoose =  this.addEditSpend;
      this.editBtnType = "addEditSpend";
      this.editSEformat();
    },

    //editEarn 选择记录收入
    editEarn: function () {
      if (!this.editChooseDetail) {
        this.editDiaShow = true;
        this.dialogMsg = "请选择消费类型";
        return;
      }
      if (!this.editMoney) {
        this.editDiaShow = true;
        this.dialogMsg = "请输入消费金额";
        return;
      }

      this.editBtnItem = true;
      // this.addchoose = this.addEditEarn;
      this.editBtnType = "addEditEarn";
      this.editSEformat();
    },
    addItem() {
      if (this.editBtnType == "addEditEarn") {
        let addEditEarn = {};
        addEditEarn.time = this.editTime;
        addEditEarn.item = this.editChooseDetail;
        addEditEarn.money = this.editMoney;
        addEditEarn.msg = this.editMsg;
        this.$store.commit("addeditEarnItems",addEditEarn);
        console.log(this.$store.state.editEarnItems);
      } else {
        let addEditSpend = {};
        addEditSpend.time = this.editTime;
        addEditSpend.item = this.editChooseDetail;
        addEditSpend.money = this.editMoney;
        addEditSpend.msg = this.editMsg;

        this.$store.commit("addeditSpendItems",addEditSpend);

        console.log(this.$store.state.editSpendItems);
      }

      this.editChooseDetail = "";
      this.editMoney = "";
      this.editMsg = "";
    },
  },
};
</script>
<style>
.editContent{
  display: grid;
  align-items: center;
  justify-items: center;
}
.editchoosetime{
    /* width:250px; */
    padding: 6px;
    font-size: 20px;
    margin-top: 6px;
    margin-bottom: 4px;
    background-color: rgba(57, 190, 119, 0.1);
/* #39be77; // 主题色 */
}

.editdetails {
  min-height: 50px;
  display: flex;
  align-items: center;
  flex-wrap:wrap;
}
.editdetails .editdetail {
  display: inline-block;
  box-sizing: border-box;
  min-width: 46px;
  height: 25px;
  font-size: 18px;
  border-radius: 15px;
  margin-left: 18px;
  text-align: center;
  background-color: rgba(195, 200, 198, 0.1);
}
.editdetails .active{
  background-color:rgba(57, 190, 119, 0.3);
}

.editdivmsg{
  font-size: 18px;
}

.editBtn {
  width: 50%;
  font-size: 1.2em;
}
.SEdialog{
  display: grid;
  justify-items: center;
  grid-template-columns:72px 180px;
  justify-content: center;
  justify-items: center;
}
.SEdialogItem{
  justify-self: end;
}

/* .editBtnActive{
 background-color:#E2F3EA ;
} */
</style>