
import { createApp} from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store/index";
import {Button, Tabbar, TabbarItem, Form, Field, Icon, NavBar, Cell, CellGroup, Popup,Dialog, List, PullRefresh, DatetimePicker, NumberKeyboard, Progress, Rate, Divider} from "vant";
// vant的键盘组件适配桌面端的mouse事件（移动端为touch事件）
import '@vant/touch-emulator';
import axios from 'axios';
import './index.css';



const app = createApp(App);
// 全局引入axios，然后注入Vue的原型对象上，就可以通过 this.$axios.get()发起请求
app.config.globalProperties.$axios = axios;

//注册已经生成实例的store和router
app.use(store);
app.use(router);
//import时，只导入需要的组件ui
//全局注册vant组件：任意子组件中可以使用已注册vant组件
app.use(Button);
app.use(Tabbar);
app.use(TabbarItem);
app.use(Form);
app.use(Field);
app.use(Icon);
app.use(NavBar);
app.use(Cell);
app.use(CellGroup);
app.use(Popup);
app.use(List);
app.use(Dialog);
app.use(PullRefresh);
app.use(DatetimePicker);
app.use(NumberKeyboard);
app.use(Progress);
app.use(Rate);
app.use(Divider);

// 挂载到html的元素上
app.mount("#app");
