
import {createStore} from 'vuex';

//token为登录界面请求数据的返回的，待定


export default createStore({
  state(){
    return{
      details: ["衣", "食", "住", "行"],
      editSpendItems:[],
      editEarnItems:[],
    }
  },
  getters:{
    getDetails:state => state.details,
    getEditSpendItems:state =>state.editSpendItems,
    getEditEarnItems:state =>state.editEarnItems,
  },
  mutations: {
    addDetails(state,paylod){
      state.details.push(paylod)
    },
    delateDetails(state,paylod){
      state.details.splice(paylod,1)
    },
    changeDetails(state,paylod){
      state.details.splice(paylod.num,1,paylod.string)      
    },
    addeditSpendItems(state,paylod){
      state.editSpendItems.push(paylod)
    },
    addeditEarnItems(state,paylod){
      state.editEarnItems.push(paylod)
    }
  },
  actions: {},
  modules: {},
});
