import { createRouter, createWebHashHistory } from "vue-router";
import login from "../views/Login.vue";

import Costs from "../views/Costs.vue";
import CostEdit from "../views/CostEdit.vue";
import CostStatics from "../views/CostStatics.vue";

//app.vue  展示页面：导向标签和login页面
// Costs.vue 标签页面（详情页面）
// CostEdit  编辑页面
// CostStatics    统计页面

const routes = [
  {
    // 配置path时：以“/”开头的嵌套路径会被当做根路径，故子路由的path不需要添加“/”-----非嵌套的，由于hash的 “#” 不可省略“/”
    //根路径，启动项目后会直接映射到App.vue的router-view
    //App.vue为展示主页面：登录和home页面
    //home页面展示：details页面，statics页面，myset页面（导航栏的三个页面）
    path: "/",  
    name: "Index",
    redirect:{name:"Costs"},
  },
  {
    path:"/login",
    name:"login",
    component:login,
  },
      {
        path:"/costs",
        name:"Costs",
        meta:{
          requireAuth:true
        },
        component:Costs,
      },

      {
        path:"/cost/edit",
        name:" CostEdit",
        component: CostEdit,
      },
      {
        path:"/cost/statics",
        name:"CostStatics",
        component:CostStatics,
      },
];

//创建路由实例
//hash模式将路径格式化为 # 开头  ---mydaily-cost项目采用hash模式
//补充知识：history模式没有 # 前缀，可以使用 pushState和replaceState管理记录
const router = createRouter({
  history: createWebHashHistory(),  
  //mode选项，hash模式：createWebHashHistory()  history模式：createWebHistory()
  routes,   
  // routes:routes的缩写
});

export default router;
